var $primaryMenu = $( '#menu-primary', '#page-header-inner' );
$primaryMenu.smartmenus( {
							 subMenusSubOffsetX: - 4,
							 subMenusSubOffsetY: 0
						 } );

$( window ).load( function() {
	$body.addClass( 'loaded' );
	setTimeout( function() {
		$( '#page-preloader' ).remove();
	}, 600 );

	$( '.tm-animation' ).waypoint( function() {
		itemQueue.push( $( this ) );
		processItemQueue();
	}, {
									   offset: '100%' // triggerOnce: true
								   } );
 
	$( '.tm-swiper' ).each( function() {
		insightInitSwiper( $( this ) );
	} );

	$( '.tm-light-gallery' ).each( function() {
		insightInitLightGallery( $( this ) );
	} );

	insightInitGrid();
} );

if ( $body.hasClass( 'one-page' ) ) {
	navOnePage();
}

// Remove empty p tags form wpautop.
$( 'p:empty' ).remove();

if ( $insight.header_sticky_enable == 1 ) {
	if ( $pageHeader.length > 0 && $pageHeader.children( '#page-header-inner' ).data( 'sticky' ) == '1' ) {
		var offset = $pageHeader.offset().top;
		$pageHeader.headroom( {
								  offset: offset
							  } );
	}
}

$( '.page-open-mainmenu' ).on( 'click', function() {
	$body.toggleClass( 'canvas-open' );
	setTimeout( function() {
		$( 'window' ).resize();
	}, 500 );
} );

var popupSearch = $( '#page-popup-search' );
$( '#btn-open-popup-search' ).on( 'click', function( e ) {
	e.preventDefault();
	popupSearch.addClass( 'popup-show' );

	$( 'body' ).addClass( 'body-noscroll' );
	setTimeout( function() {
		popupSearch.find( '.search-field' ).focus();
	}, 500 )
} );

$( '#popup-search-close' ).on( 'click', function( e ) {
	e.preventDefault();
	popupSearch.removeClass( 'popup-show' );
	$( 'body' ).removeClass( 'body-noscroll' );
} );

$( '.popup-video' ).lightGallery( {
									  selector      : 'this',
									  iframeMaxWidth: '80%'
								  } );

$( '#music_toggle' ).on( 'click', function( e ) {
	e.preventDefault();
	var self      = $( this );
	var siteAudio = $( '#site-background-sound' );
	if ( siteAudio.length > 0 ) {
		var theAudio = siteAudio.get( 0 );
		if ( theAudio.paused == false ) {
			siteAudio.prop( 'volume', 1 );
			self.removeClass( 'on' );
			var volume = 1;
			var loop   = setInterval( function() {
				if ( volume <= 0.1 ) {
					clearInterval( loop );
					theAudio.pause();
				} else {
					volume = (
								 volume * 10 - 1
							 ) / 10;
					siteAudio.prop( 'volume', volume );
				}
			}, 200 );

		} else {
			var volume = 0;
			siteAudio.prop( 'volume', volume );
			theAudio.play();
			self.addClass( 'on' );
			var loop = setInterval( function() {
				if ( volume >= 0.9 ) {
					clearInterval( loop );
				} else {
					volume = (
								 volume * 10 + 1
							 ) / 10;
					siteAudio.prop( 'volume', volume );
				}
			}, 200 );
		}
	}
} );

$( '.counter' ).each( function() {
	$( this ).counterUp( {
							 delay: 10,
							 time : 1000
						 } );
} );

$( '.hover-dir' ).hoverdir( {
								hoverElem: '.post-overlay',
								speed    : 500,
								easing   : 'cubic-bezier(0.645, 0.045, 0.355, 1)'
							} );

// Allows for easy implementation of smooth scrolling for buttons.
$( '.smooth-scroll-link' ).on( 'click', function( e ) {
	var href = $( this ).attr( 'href' );

	if ( href.match( /^([.#])(.+)/ ) ) {
		e.preventDefault();
		var $headerInner = $( '#page-header-inner' );

		var offset = 0;

		if ( $insight.header_sticky_enable == 1 && $headerInner.data( 'sticky' ) == '1' ) {
			offset = $headerInner.height();
			offset -= 1;
			offset = - offset;
		}

		$.smoothScroll( {
							offset      : offset,
							scrollTarget: $( href ),
							speed       : 600,
							easing      : 'linear',
						} );
	}
} );

if ( $insight.scroll_top_enable == 1 ) {
	scrollToTop();
}

initMobileMenu();

function insightInitLightGallery( $gallery ) {
	$gallery.lightGallery( {
							   selector                : '.zoom',
							   thumbnail               : true,
							   download                : true,
							   animateThumb            : false,
							   showThumbByDefault      : false,
							   getCaptionFromTitleOrAlt: false
						   } );
}

function insightInitSwiper( $slider ) {
	var $sliderContainer = $slider.children( '.swiper-container' ).first();
	var lgItems          = $slider.data( 'lg-items' ) ? $slider.data( 'lg-items' ) : 1;
	var mdItems          = $slider.data( 'md-items' ) ? $slider.data( 'md-items' ) : lgItems;
	var smItems          = $slider.data( 'sm-items' ) ? $slider.data( 'sm-items' ) : mdItems;
	var xsItems          = $slider.data( 'xs-items' ) ? $slider.data( 'xs-items' ) : smItems;

	var lgGutter = $slider.data( 'lg-gutter' ) ? $slider.data( 'lg-gutter' ) : 0;
	var mdGutter = $slider.data( 'md-gutter' ) ? $slider.data( 'md-gutter' ) : lgGutter;
	var smGutter = $slider.data( 'sm-gutter' ) ? $slider.data( 'sm-gutter' ) : mdGutter;
	var xsGutter = $slider.data( 'xs-gutter' ) ? $slider.data( 'xs-gutter' ) : smGutter;

	var vertical   = $slider.data( 'vertical' );
	var loop       = $slider.data( 'loop' );
	var autoPlay   = $slider.data( 'autoplay' );
	var nav        = $slider.data( 'nav' );
	var pagination = $slider.data( 'pagination' );
	var mouseWheel = $slider.data( 'mousewheel' );

	var options = {
		slidesPerView: lgItems,
		spaceBetween : lgGutter,
		breakpoints  : {
			// when window width is <=
			767 : {
				slidesPerView: xsItems,
				spaceBetween : xsGutter
			},
			990 : {
				slidesPerView: smItems,
				spaceBetween : smGutter
			},
			1199: {
				slidesPerView: mdItems,
				spaceBetween : mdGutter
			}
		}
	};

	if ( loop ) {
		options.loop = true;
	}

	if ( autoPlay ) {
		options.autoplay                     = autoPlay;
		options.autoplayDisableOnInteraction = false;
	}

	if ( nav ) {
		options.prevButton = $slider.children( '.swiper-button-prev' );
		options.nextButton = $slider.children( '.swiper-button-next' );
	}

	if ( pagination ) {
		options.pagination          = $slider.children( '.swiper-pagination' );
		options.paginationClickable = true;
	}

	if ( mouseWheel ) {
		options.mousewheelControl = true;
	}

	if ( vertical ) {
		options.direction = 'vertical'
	}

	var $swiper = new Swiper( $sliderContainer, options );
}

function animateMagicLineOnScroll( $li, $magicLine, onScroll, id ) {
	if ( onScroll == false ) {
		$li.each( function() {
			var link = $( this ).children( 'a[href*="#"]:not([href="#"])' );
			if ( link.attr( 'href' ) == id ) {

				if ( $magicLine ) {
					var left  = $( this ).position().left + link.padding().left;
					var width = link.width();
					$magicLine.stop().animate( {
												   left : left,
												   width: width
											   } );
					$magicLine
						.attr( 'data-left', left )
						.attr( 'data-width', width );
				}

				$( this ).siblings( 'li' ).removeClass( 'current-menu-item' );
				$( this ).addClass( 'current-menu-item' );

				return true;
			}
		} );
	}
}

function navOnePage() {
	var $header                 = $( '#page-header' );
	var $headerInner            = $header.children( '#page-header-inner' );
	var isMagicLine             = $headerInner.data( 'magic-line' );
	var $el, newWidth, $mainNav = $( '#page-navigation' ).find( '.menu__container' ).first();
	var $li                     = $mainNav.children( '.menu-item' );
	var $links                  = $li.children( 'a[href*="#"]:not([href="#"])' );
	var onScroll                = false;
	var $magicLine;

	if ( isMagicLine ) {
		$mainNav.append( '<li id="magic-line"></li>' );
		$magicLine = $( '#magic-line' );
	}

	$li.each( function() {
		var link = $( this ).children( 'a[href*="#"]:not([href="#"])' );
		var id   = link.attr( 'href' );
		if ( $( id ).length > 0 ) {
			$( id ).waypoint( function( direction ) {
				if ( direction === 'down' ) {
					animateMagicLineOnScroll( $li, $magicLine, onScroll, id );
				}
			}, {
								  offset: '25%'
							  } );

			$( id ).waypoint( function( direction ) {
				if ( direction === 'up' ) {
					animateMagicLineOnScroll( $li, $magicLine, onScroll, id );
				}
			}, {
								  offset: '-25%'
							  } );
		}
	} );

	if ( $magicLine ) {
		$li.hover( function() {
			$el = $( this );

			var link = $el.children( 'a' );
			var left = $( this ).position().left + link.padding().left;
			newWidth = $el.children( 'a' ).width();
			$magicLine.stop().animate( {
										   left : left,
										   width: newWidth
									   } );
		}, function() {
			if ( ! $( this ).hasClass( 'current-menu-item' ) ) {
				$magicLine.stop().animate( {
											   left : $magicLine.attr( 'data-left' ),
											   width: $magicLine.attr( 'data-width' )
										   } );
			}
		} );
	}

	// Allows for easy implementation of smooth scrolling for navigation links.
	$links.on( 'click', function() {
		var $this  = $( this );
		var href   = $( this ).attr( 'href' );
		var offset = 0;

		if ( $insight.header_sticky_enable == 1 && $headerInner.data( 'sticky' ) == '1' ) {
			// Check if user don't click on home link.
			if ( href !== '#page' ) {
				$header.addClass( 'headroom--not-top' ).removeClass( 'headroom--top' );
			}
			offset = $headerInner.height();
			offset -= 1;
			offset = - offset;
		}

		var parent = $this.parent( 'li' );

		if ( $magicLine ) {
			var left = parent.position().left + $this.padding().left;
			$magicLine.attr( 'data-left', left )
					  .attr( 'data-width', $this.width() );
		}

		parent.siblings( 'li' ).removeClass( 'current-menu-item' );
		parent.addClass( 'current-menu-item' );

		$.smoothScroll( {
							offset      : offset,
							scrollTarget: $( href ),
							speed       : 600,
							easing      : 'linear',
							beforeScroll: function() {
								onScroll = true;
							},
							afterScroll : function() {
								onScroll = false;
							}
						} );
		return false;
	} );
}

function scrollToTop() {
	var $window        = $( window );
	// Scroll up
	var $scrollup      = $( '.scrollup' );
	var $scrollupFixed = $( '.scrollup--fixed' );

	$window.scroll( function() {
		if ( $window.scrollTop() > 100 ) {
			$scrollupFixed.addClass( 'show' );
		} else {
			$scrollupFixed.removeClass( 'show' );
		}
	} );

	$scrollup.on( 'click', function( evt ) {
		$( 'html, body' ).animate( { scrollTop: 0 }, 600 );
		evt.preventDefault();
	} );
}

function initMobileMenu() {
	$( '#page-open-mobile-menu' ).on( 'click', function( e ) {
		e.preventDefault();
		$body.addClass( 'page-mobile-menu-opened' );
	} );

	$( '#page-close-mobile-menu' ).on( 'click', function( e ) {
		e.preventDefault();
		$body.removeClass( 'page-mobile-menu-opened' );
	} );

	var mobileMobile = $( '#mobile-menu-primary' );

	mobileMobile.on( 'click', '.menu-item-has-children > a', function( e ) {
		e.preventDefault();
		e.stopPropagation();

		var _li = $( this ).parent( 'li' );

		if ( _li.hasClass( 'opened' ) ) {
			_li.removeClass( 'opened' );
			_li.find( '.opened' ).removeClass( 'opened' );
			_li.find( '.sub-menu' ).stop().slideUp();
		} else {
			var _parent = _li.parent( 'ul' );

			// If level 1 clicked, slide up other items.
			if ( ! _parent.hasClass( 'sub-menu' ) ) {
				var li_opened = _li.siblings( '.opened' );
				li_opened.removeClass( 'opened' );
				li_opened.find( '.opened' ).removeClass( 'opened' );
				li_opened.find( '.sub-menu' ).stop().slideUp();
			}

			_li.addClass( 'opened' );
			_li.children( '.sub-menu' ).stop().slideDown();
		}

	} );
}
